import React from "react";
import ReactDom from 'react-dom';
import axios from 'axios';
import VidList from "./form";
import './styles.css'
class Youtube extends React.Component{
    state={text:'' ,response:null, matches:0,video:null,title:'',description:'',url:null}
    makeAPIrequest =async (txt)=>{
    const response = await axios.get('https://www.googleapis.com/youtube/v3/search',{
        params:{
            q:txt,
            type:'video',
            key:'AIzaSyBXAyimeyDQaq0xckMpjEG4NzYGJR_XTbg',
            part:'snippet',
            maxResults:20
        }

    })
    console.log(response.data.items)
    this.setState({response:response.data.items,matches:response.data.items.length})
    }
    submitted=(event)=>{
        event.preventDefault();
        this.makeAPIrequest(this.state.text)
    }
    refresh=(video,title,desc,url)=>{
        this.setState({SelectedVid:video,title:title,description:desc,url:url})
    }
    render(){
        return<div className='All'>
            <form onSubmit={this.submitted} className="ui form">
        <div className="field">
          <label>Look for your favorite videos</label>
          <input value={this.state.text} onChange={(e)=> this.setState({text:e.target.value})} type="text"/>
        <div> We found : {this.state.matches}  Matches </div>
        </div>
      </form>
      <iframe width="70%" height="250px"
src={`https://www.youtube.com/embed/${this.state.url}`}>
</iframe><div className='desc'><div style={{fontSize:'110%'}}>{this.state.title}</div>
           </div>
            <VidList videos={this.state.response} refresh={this.refresh}/>
            
        </div>
    }
    
}
ReactDom.render(<Youtube/>,document.querySelector('#root'));