import React from "react";
import VideoPlay from "./video";
const VidList = (props)=>{
    if (props.videos){

    
    const list=props.videos.map((video)=>{
       return(
            <div onClick={()=>props.refresh(video,video.snippet.title,video.snippet.description,video.id.videoId)} className="ui comments">
  <h3 className="ui dividing header"></h3>
  <div className="comment">
    <a className="">
    <img src={video.snippet.thumbnails.medium.url}/>
    </a>
    <div className="content">
      <a style={{fontSize:'50%', display:'inline', marginTop:'-80px',marginLeft:'0px'}} className="author">{video.snippet.title}</a>
      
     
      
    </div>
  </div>
            
            </div>
       )
       
    })
    return <div className='vidlist'>{list}</div>
}
    return null
}
export default VidList;